import {Dispatch} from "redux";
import {authAPI, LoginParamsType} from "../../api/authAPI";
import {setAppStatusAC} from "../../app/app-reducer";
import {AxiosError} from "axios";
import {FieldErrorType, ResultCodeStatus} from "../../api/todolists-api";
import {handleServerAppError, handleServerNetworkError} from "../../utils/error-utils";
import {createAsyncThunk, createSlice, PayloadAction} from "@reduxjs/toolkit";


export const loginTC =
    createAsyncThunk<undefined, LoginParamsType, { rejectValue: { error: Array<string>, fieldsError?: Array<FieldErrorType> } }>
    ('auth/login', async (param, thunkAPI) => {
        thunkAPI.dispatch(setAppStatusAC({status: 'loading'}))
        const res = await authAPI.login(param)
        try {
            if (res.data.resultCode === ResultCodeStatus.succeed) {
                thunkAPI.dispatch(setAppStatusAC({status: 'succeeded'}))
                return;

            } else if (res.data.resultCode === ResultCodeStatus.failed) {
                handleServerAppError(res.data, thunkAPI.dispatch)
                return thunkAPI.rejectWithValue({error: res.data.messages, fieldsError: res.data.fieldsErrors})
            }

        } catch (e) {
            let error = e as AxiosError
            handleServerNetworkError(error, thunkAPI.dispatch)
            return thunkAPI.rejectWithValue({error: [error.message], fieldsError: undefined})
        }
    })


export const logOutTC = createAsyncThunk('auth/logout', async (param, thunkAPI) => {
    thunkAPI.dispatch(setAppStatusAC({status: 'loading'}))
    const res = await authAPI.logOut()
    try {
        if (res.data.resultCode === 0) {
            thunkAPI.dispatch(setAppStatusAC({status: 'succeeded'}))
            return;
        } else {
            handleServerAppError(res.data, thunkAPI.dispatch)
            return thunkAPI.rejectWithValue({})
        }
    } catch (e) {
        const error = e as AxiosError
        handleServerNetworkError(error, thunkAPI.dispatch)
        return thunkAPI.rejectWithValue({})
    }
})



const slice = createSlice({
    name: 'auth',
    initialState: {
        isLoggedIn: false
    },
    reducers: {
        setIsLoggedInAC(state, action: PayloadAction<{ value: boolean }>) {
            state.isLoggedIn = action.payload.value
        }
    },
    extraReducers: builder => {
        builder.addCase(loginTC.fulfilled, (state ) => {
            state.isLoggedIn = true
        })
        builder.addCase(logOutTC.fulfilled, (state) => {
            state.isLoggedIn = false
        })
    }
})

export const authLoginReducer = slice.reducer
export const {setIsLoggedInAC} = slice.actions



