import {todolistsAPI, TodolistType} from '../../api/todolists-api'
import {RequestStatusType, setAppStatusAC} from '../../app/app-reducer'
import {createAsyncThunk, createSlice, PayloadAction} from "@reduxjs/toolkit";

export const fetchTodolistsTC = createAsyncThunk('todolist/fetchTodolists', async (param, thunkAPI) => {
    thunkAPI.dispatch(setAppStatusAC({status: 'loading'}))
    const res = await todolistsAPI.getTodolists()
    thunkAPI.dispatch(setAppStatusAC({status: 'succeeded'}))
    return {todoList: res.data}
})
export const removeTodolistTC = createAsyncThunk('todolist/removeTodolists', async (param: { todolistId: string }, {dispatch}) => {
    dispatch(setAppStatusAC({status: 'loading'}))
    dispatch(changeTodolistEntityStatusAC({id: param.todolistId, status: 'loading'}))
    const res = await todolistsAPI.deleteTodolist(param.todolistId)
    dispatch(setAppStatusAC({status: 'succeeded'}))
    return {id: param.todolistId}
})
export const addTodolistTC = createAsyncThunk('todolist/addTodolists', async (param: { title: string }, {dispatch}) => {
    dispatch(setAppStatusAC({status: 'loading'}))
    const res = await todolistsAPI.createTodolist(param.title)
    dispatch(setAppStatusAC({status: 'succeeded'}))
    return {todolist: res.data.data.item}
})
export const changeTodolistTitleTC =createAsyncThunk('todolist/changeTodolists', async (param: { id: string, title: string }, thunkAPI) => {
   const res = await todolistsAPI.updateTodolist(param.id, param.title)
            return {id: param.id, title: param.title}
})



const initialState: Array<TodolistDomainType> = []
const slice = createSlice({
    name: 'todoLists',
    initialState: initialState,
    reducers: {
        changeTodolistTitleAC(state, action: PayloadAction<{ id: string, title: string }>) {
            const index = state.findIndex(el => el.id === action.payload.id)
            state[index].title = action.payload.title
        },
        changeTodolistFilterAC(state, action: PayloadAction<{ id: string, filter: FilterValuesType }>) {
            const index = state.findIndex(el => el.id === action.payload.id)
            state[index].filter = action.payload.filter
        },
        changeTodolistEntityStatusAC(state, action: PayloadAction<{ id: string, status: RequestStatusType }>) {
            const index = state.findIndex(el => el.id === action.payload.id)
            state[index].entityStatus = action.payload.status
        },


    },
    extraReducers: builder => {
        builder.addCase(fetchTodolistsTC.fulfilled, (state, action) => {
            return action.payload.todoList.map(tl => ({...tl, filter: 'all', entityStatus: 'idle'}))
        })
        builder.addCase(removeTodolistTC.fulfilled, (state, action) => {
            const index = state.findIndex(el => el.id === action.payload.id)
            if (index > -1) {
                state.splice(index, 1)
            }
        })
        builder.addCase(addTodolistTC.fulfilled, (state, action) => {
            state.unshift({...action.payload.todolist, filter: 'all', entityStatus: 'idle'})
        })
        builder.addCase(changeTodolistTitleTC.fulfilled, (state, action) => {
            const index = state.findIndex(el => el.id === action.payload.id)
            state[index].title = action.payload.title
        })
    }
})

export const {
    changeTodolistFilterAC, changeTodolistEntityStatusAC
} = slice.actions
export const todolistsReducer = slice.reducer




export type FilterValuesType = 'all' | 'active' | 'completed';
export type TodolistDomainType = TodolistType & {
    filter: FilterValuesType
    entityStatus: RequestStatusType
}

